import { INC, DEC, RANGE } from './types'

const defaultState = {
  counter: 0,
  rangeVal: 1,
}

const reducer = (state = defaultState, action) => {
  switch (action.type) {
    case INC:
      return {
        ...state,
        counter: state.counter + state.rangeVal,
      }
    case DEC:
      return {
        ...state,
        counter: state.counter - state.rangeVal,
      }
    case RANGE:
      return {
        ...state,
        rangeVal: action.payload,
      }
    default:
      return state
  }
}

export default reducer
