import React from 'react'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'

import * as actions from '../actions'

import InputRange from './InputRange'

const Counter = ({ counter, rangeVal, inc, dec, range }) => {
  return (
    <div className='counter container py-3 text-center'>
      <div className='row'>
        <div className='col-10 col-sm-7 col-md-5 col-lg-4 mx-auto'>
          <p>
            Значение счётчика должно увеличиваться или уменьшаться на заданную
            величину шага
          </p>
          <p>
            Текущая величина шага: <b>{rangeVal}</b>
          </p>
        </div>
      </div>
      <div className='row counter__range'>
        <div className='col col-10 col-sm-7 col-md-5 col-lg-4 mx-auto'>
          <InputRange range={range} />
        </div>
      </div>
      <div className='row counter__controls'>
        <div className='d-flex justify-content-center align-items-center'>
          <button className='btn btn-outline-primary btn-lg' onClick={dec}>
            &ndash;
          </button>
          <b
            className='counter__number d-inline-block mx-1 display-6'
            style={{ width: '60px' }}
          >
            {counter}
          </b>
          <button className='btn btn-outline-primary btn-lg' onClick={inc}>
            +
          </button>
        </div>
      </div>
    </div>
  )
}

Counter.propTypes = {
  counter: PropTypes.number,
  rangeVal: PropTypes.number,
  inc: PropTypes.func.isRequired,
  dec: PropTypes.func.isRequired,
}

const mapStateToProps = (state) => {
  return {
    counter: state.counter,
    rangeVal: state.rangeVal,
  }
}

export default connect(mapStateToProps, actions)(Counter)
